
import db from './db'; // Import the initialized DB instance
import type { Event, User, Participant, Announcement } from './types';
import { parseISO } from 'date-fns';

// --- User Functions ---
export async function getUsers(): Promise<User[]> {
  const stmt = db.prepare('SELECT id, username, avatarUrl, op, password FROM users');
  return stmt.all() as User[];
}

export async function getUserById(userId: string): Promise<User | undefined> {
  const stmt = db.prepare('SELECT id, username, avatarUrl, op, password FROM users WHERE id = ?');
  return stmt.get(userId) as User | undefined;
}

export async function getUserByUsername(username: string): Promise<User | undefined> {
  const stmt = db.prepare('SELECT id, username, avatarUrl, op, password FROM users WHERE lower(username) = lower(?)');
  return stmt.get(username) as User | undefined;
}

export async function createUser(userData: Omit<User, 'op' | 'avatarUrl'> & Partial<Pick<User, 'op' | 'avatarUrl' | 'password'>>): Promise<User> {
  const existingUser = await getUserByUsername(userData.username);
  if (existingUser) {
    console.warn(`Attempting to create an already existing user: ${userData.username} (ID: ${userData.id}). Returning existing user.`);
    return existingUser;
  }

  const newUser: User = {
    id: userData.id || userData.username,
    username: userData.username,
    avatarUrl: userData.avatarUrl || `https://placehold.co/100x100.png?text=${userData.username.substring(0, 2).toUpperCase()}`,
    op: userData.op || 0,
    password: userData.password, // Storing password directly for prototype
  };

  const stmt = db.prepare('INSERT INTO users (id, username, avatarUrl, op, password) VALUES (@id, @username, @avatarUrl, @op, @password)');
  try {
    stmt.run(newUser);
    return newUser;
  } catch (error) {
    console.error(`Failed to create user ${userData.username}:`, error);
    // Re-throw or handle more gracefully depending on desired app behavior
    throw new Error(`Failed to create user ${userData.username}: ${(error as Error).message}`);
  }
}

export async function updateUserById(userId: string, userData: Partial<Pick<User, 'username' | 'avatarUrl' | 'op' | 'password'>>): Promise<User | null> {
  const currentUser = await getUserById(userId);
  if (!currentUser) return null;

  const fieldsToUpdate: Record<string, any> = {};
  if (userData.username !== undefined) fieldsToUpdate.username = userData.username;
  if (userData.avatarUrl !== undefined) fieldsToUpdate.avatarUrl = userData.avatarUrl;
  if (userData.op !== undefined) fieldsToUpdate.op = userData.op;
  if (userData.password !== undefined && userData.password !== '') fieldsToUpdate.password = userData.password; // Update password only if provided and not empty

  if (Object.keys(fieldsToUpdate).length === 0) {
    return currentUser; // No actual changes to make
  }

  const setClauses = Object.keys(fieldsToUpdate).map(key => `${key} = @${key}`).join(', ');
  const stmt = db.prepare(`UPDATE users SET ${setClauses} WHERE id = @id`);
  
  const result = stmt.run({ ...fieldsToUpdate, id: userId });
  if (result.changes === 0) return null; // Should not happen if user exists and there are fields to update

  return getUserById(userId); // Fetch and return updated user
}

export async function deleteUserById(userId: string): Promise<{ success: boolean, message: string }> {
  // Check if the user is an organizer of any event
  const eventOrganizerCheckStmt = db.prepare('SELECT COUNT(*) as count FROM events WHERE organizerId = ?');
  const organizerCount = eventOrganizerCheckStmt.get(userId) as { count: number };

  if (organizerCount.count > 0) {
    return { success: false, message: `无法删除用户 ${userId}，因为他们是 ${organizerCount.count} 个活动的组织者。请先转移或删除这些活动。` };
  }

  const deleteUserStmt = db.prepare('DELETE FROM users WHERE id = ?');
  const result = deleteUserStmt.run(userId);
  // CASCADE delete on event_participants table will handle removing their participation records.

  if (result.changes > 0) {
    return { success: true, message: `用户 ${userId} 已成功删除。` };
  }
  return { success: false, message: `用户 ${userId} 未找到，无法删除。` };
}


// --- Event Functions ---
async function getEventParticipants(eventId: string): Promise<Participant[]> {
  const stmt = db.prepare(`
    SELECT u.id, u.username, u.avatarUrl, u.op, ep.isCheckedIn
    FROM users u
    JOIN event_participants ep ON u.id = ep.userId
    WHERE ep.eventId = ?
  `);
  // Note: We don't select u.password here for participants list
  const participantsData = stmt.all(eventId) as Array<Omit<Participant, 'joinedEventIds' | 'password'>>;
  return participantsData.map(p => ({...p, isCheckedIn: !!p.isCheckedIn}));
}

export async function getEvents(): Promise<Event[]> {
  const eventsStmt = db.prepare('SELECT * FROM events');
  const rawEvents = eventsStmt.all() as Array<Omit<Event, 'participants'>>;
  
  const events: Event[] = [];
  for (const rawEvent of rawEvents) {
    const participants = await getEventParticipants(rawEvent.id);
    events.push({ ...rawEvent, participants });
  }
  return events;
}

export async function getEventById(id: string): Promise<Event | undefined> {
  const eventStmt = db.prepare('SELECT * FROM events WHERE id = ?');
  const rawEvent = eventStmt.get(id) as Omit<Event, 'participants'> | undefined;

  if (!rawEvent) return undefined;

  const participants = await getEventParticipants(rawEvent.id);
  return { ...rawEvent, participants };
}

export async function getEventsForUser(userId: string): Promise<Event[]> {
  const stmt = db.prepare(`
    SELECT e.*
    FROM events e
    JOIN event_participants ep ON e.id = ep.eventId
    WHERE ep.userId = ?
  `);
  const rawEvents = stmt.all(userId) as Array<Omit<Event, 'participants'>>;
  
  const joinedEvents: Event[] = [];
  for (const rawEvent of rawEvents) {
    // For each event the user joined, we still need to fetch its full participant list
    // to display on event cards or detail pages.
    const participants = await getEventParticipants(rawEvent.id);
    joinedEvents.push({ ...rawEvent, participants });
  }
  return joinedEvents;
}


export async function getEventByCdKey(cdKey: string): Promise<Event | undefined> {
  const eventStmt = db.prepare('SELECT * FROM events WHERE lower(cdKey) = lower(?)');
  const rawEvent = eventStmt.get(cdKey) as Omit<Event, 'participants'> | undefined;

  if (!rawEvent) return undefined;

  const participants = await getEventParticipants(rawEvent.id);
  return { ...rawEvent, participants };
}

export async function createEvent(eventData: Omit<Event, 'id' | 'participants' | 'organizerId'>, organizerId: string): Promise<Event> {
  const newEventId = `event_${Date.now()}_${Math.random().toString(36).substring(2, 7)}`;
  const participantLimit = typeof eventData.participantLimit === 'number' && !isNaN(eventData.participantLimit) ? eventData.participantLimit : null;

  const newEventRow = {
    id: newEventId,
    name: eventData.name,
    date: eventData.date,
    description: eventData.description,
    participantLimit: participantLimit,
    cdKey: eventData.cdKey,
    imageUrl: eventData.imageUrl || '',
    organizerId,
  };
  const stmt = db.prepare('INSERT INTO events (id, name, date, description, participantLimit, cdKey, imageUrl, organizerId) VALUES (@id, @name, @date, @description, @participantLimit, @cdKey, @imageUrl, @organizerId)');
  stmt.run(newEventRow);
  return { ...newEventRow, participants: [] }; // Return with empty participants as they are managed separately
}

export async function updateEventById(eventId: string, eventData: Partial<Omit<Event, 'id' | 'participants' | 'organizerId'>>): Promise<Event | null> {
  const { name, date, description, participantLimit, cdKey, imageUrl } = eventData;
  const actualParticipantLimit = typeof participantLimit === 'number' && !isNaN(participantLimit) ? participantLimit : (participantLimit === null ? null : undefined);


  const fieldsToUpdate: Record<string, any> = {};
  if (name !== undefined) fieldsToUpdate.name = name;
  if (date !== undefined) fieldsToUpdate.date = date;
  if (description !== undefined) fieldsToUpdate.description = description;
  if (actualParticipantLimit !== undefined) fieldsToUpdate.participantLimit = actualParticipantLimit; // Can be null
  if (cdKey !== undefined) fieldsToUpdate.cdKey = cdKey;
  if (imageUrl !== undefined) fieldsToUpdate.imageUrl = imageUrl;

  if (Object.keys(fieldsToUpdate).length === 0) {
    return getEventById(eventId) || null; // No actual changes
  }

  const setClauses = Object.keys(fieldsToUpdate).map(key => `${key} = @${key}`).join(', ');
  const stmt = db.prepare(`UPDATE events SET ${setClauses} WHERE id = @eventId`);
  
  const result = stmt.run({ ...fieldsToUpdate, eventId });
  
  if (result.changes === 0) return null;
  return getEventById(eventId) || null;
}

export async function deleteEventById(eventId: string): Promise<{ success: boolean, message: string }> {
  const stmt = db.prepare('DELETE FROM events WHERE id = ?');
  const result = stmt.run(eventId);

  if (result.changes > 0) {
    return { success: true, message: "活动已成功删除。" };
  }
  return { success: false, message: "活动未找到，无法删除。" };
}

export async function joinEvent(userId: string, eventId: string, cdKeyInput: string): Promise<{ success: boolean, message: string, event?: Event }> {
  const event = await getEventById(eventId);
  if (!event) return { success: false, message: '活动未找到。' };

  const user = await getUserById(userId);
  if (!user) return { success: false, message: '用户未找到。' };

  if (event.cdKey.toLowerCase() !== cdKeyInput.toLowerCase()) return { success: false, message: '无效的活动代码。' };
  
  const currentParticipants = await getEventParticipants(eventId);
  if (event.participantLimit && currentParticipants.length >= event.participantLimit) {
    return { success: false, message: '活动人数已满。' };
  }
  if (currentParticipants.some(p => p.id === userId)) {
    // If user is already a participant, just return success and current event state
    return { success: true, message: '您已加入此活动。', event };
  }

  const stmt = db.prepare('INSERT INTO event_participants (eventId, userId, isCheckedIn) VALUES (?, ?, 0)');
  stmt.run(eventId, userId);
  
  const finalEvent = await getEventById(eventId); // Re-fetch to get updated participant list
  return { success: true, message: '成功加入活动！', event: finalEvent };
}

export async function leaveEvent(userId: string, eventId: string): Promise<{ success: boolean, message: string, event?: Event }> {
  const event = await getEventById(eventId);
  if (!event) return { success: false, message: '活动未找到。' };

  const stmt = db.prepare('DELETE FROM event_participants WHERE eventId = ? AND userId = ?');
  const result = stmt.run(eventId, userId);

  if (result.changes > 0) {
    const updatedEvent = await getEventById(eventId);
    return { success: true, message: '成功离开活动。', event: updatedEvent };
  }
  return { success: false, message: '您当前未加入此活动，或离开失败。' };
}

export async function checkInUser(userId: string, eventId: string): Promise<{ success: boolean, message: string }> {
  const event = await getEventById(eventId);
  if (!event) return { success: false, message: '活动未找到。' };

  const participants = await getEventParticipants(eventId);
  const participant = participants.find(p => p.id === userId);

  if (!participant) return { success: false, message: '您未报名参加此活动。' };
  if (participant.isCheckedIn) return { success: true, message: '您已签到。' };

  // Check if the event is currently happening (same day)
  const eventDateObj = parseISO(event.date);
  const today = new Date();
  
  // Compare year, month, and day only for "current" status
  const isCurrent = eventDateObj.getFullYear() === today.getFullYear() &&
                    eventDateObj.getMonth() === today.getMonth() &&
                    eventDateObj.getDate() === today.getDate();

  if (!isCurrent) {
    return { success: false, message: '此活动的签到尚未开始或已结束。' };
  }

  const stmt = db.prepare('UPDATE event_participants SET isCheckedIn = 1 WHERE eventId = ? AND userId = ?');
  stmt.run(eventId, userId);
  return { success: true, message: '签到成功！' };
}

// --- Announcement Functions ---
export async function getAnnouncements(): Promise<Announcement[]> {
  const stmt = db.prepare('SELECT * FROM announcements ORDER BY date DESC');
  return stmt.all() as Announcement[];
}
