const MAX_AGE = 60 * 60 * 24 * 30; // 30 days

// --- Geetest Verification ---
async function verifyGeetestV4(
  lot_number?: string, 
  captcha_output?: string, 
  pass_token?: string, 
  gen_time?: string
): Promise<boolean> {
  const captchaId = process.env.NEXT_PUBLIC_GEETEST_CAPTCHA_ID;
  const captchaKey = process.env.GEETEST_SECRET_KEY;

  if (!captchaId || !captchaKey) {
    console.warn("Geetest CAPTCHA_ID or SECRET_KEY not configured. Skipping validation in dev/test mode.");
    return process.env.NODE_ENV !== 'production'; 
  }
  if (!lot_number || !captcha_output || !pass_token || !gen_time) {
    console.warn("Geetest client parameters missing for server validation.");
    return false;
  }
  console.log("Server-side Geetest validation: INITIATED");
  console.log("Received Geetest params:", {lot_number, captcha_output, pass_token, gen_time, captchaIdUsed: captchaId});


  const params = new URLSearchParams();
  params.append('lot_number', lot_number);
  params.append('captcha_output', captcha_output);
  params.append('pass_token', pass_token);
  params.append('gen_time', gen_time);
  params.append('captcha_id', captchaId);
  
  const sign_token = crypto.createHmac('sha256', captchaKey)
    .update(lot_number)
    .digest('hex');
  params.append('sign_token', sign_token);

  try {
    const response = await fetch('https://gcaptcha4.geetest.com/validate', {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: params.toString(),
    });
    if (!response.ok) {
      const errorBody = await response.text();
      console.error(`Geetest API request failed with status: ${response.status}. Body: ${errorBody}`);
      return false;
    }
    const data = await response.json();
    console.log("Geetest validation API response:", data);
    if (data?.status === 'success' && data?.result === 'success') {
      console.log("Server-side Geetest validation: SUCCESS");
      return true;
    } else {
      console.warn("Server-side Geetest validation: FAILED. Response data:", data);
      return false;
    }
  } catch (error) {
    console.error('Geetest validation API request failed:', error);
    return false;
  }
}

// --- Authentication ---
const MinecraftUsernameSchema = z.string()
  .min(3, "Minecraft 用户名长度必须在 3 到 16 个字符之间。")
  .max(16, "Minecraft 用户名长度必须在 3 到 16 个字符之间。")
  .regex(/^[a-zA-Z0-9_]+$/, "Minecraft 用户名只能包含字母、数字和下划线。")
  .refine(username => !username.startsWith('_') && !username.endsWith('_'), {
    message: "Minecraft 用户名不能以下划线开头或结尾。",
  });

const AuthFormSchema = z.object({
  minecraftUsername: MinecraftUsernameSchema,
  password: z.string().min(6, "密码至少需要6个字符。"),
  confirmPassword: z.string().optional(),
  authMode: z.enum(['login', 'register']),
  lot_number: z.string().optional(), // Geetest param
  captcha_output: z.string().optional(), // Geetest param
  pass_token: z.string().optional(), // Geetest param
  gen_time: z.string().optional(), // Geetest param
}).refine(data => {
  if (data.authMode === 'register') {
    return data.password === data.confirmPassword;
  }
  return true;
}, {
  message: "两次输入的密码不匹配。",
  path: ["confirmPassword"], // Apply error to confirmPassword field if mismatch in register mode
});

export type AuthFormState = {
  message?: string | null;
  errors?: {
    minecraftUsername?: string[];
    password?: string[];
    confirmPassword?: string[];
    authMode?: string[];
    form?: string[];
    captcha?: string[];
  };
  success: boolean;
  userId?: string;
};

export async function authenticateUserAction(prevState: AuthFormState, formData: FormData): Promise<AuthFormState> {
  console.log("authenticateUserAction: Received formData entries:");
  for (const [key, value] of formData.entries()) {
    console.log(`  ${key}: ${value}`);
  }
  
  try {
    const rawFormData = Object.fromEntries(formData.entries());
    const validatedFields = AuthFormSchema.safeParse(rawFormData);

    if (!validatedFields.success) {
      console.error("authenticateUserAction: Zod validation failed:", validatedFields.error.flatten().fieldErrors);
      return {
        message: '输入无效。请检查您填写的信息。',
        errors: validatedFields.error.flatten().fieldErrors,
        success: false,
      };
    }
    
    const { minecraftUsername, password, authMode, lot_number, captcha_output, pass_token, gen_time } = validatedFields.data;

    const isCaptchaValid = await verifyGeetestV4(lot_number, captcha_output, pass_token, gen_time);
    if (!isCaptchaValid) {
      console.warn("authenticateUserAction: Geetest validation failed.");
      return {
        message: '人机验证失败，请重试。',
        errors: { captcha: ['人机验证失败或已过期，请刷新验证码后重试。'] },
        success: false,
      };
    }
    console.log("authenticateUserAction: Geetest validation successful.");

    if (authMode === 'login') {
      console.log(`authenticateUserAction: Attempting login for user "${minecraftUsername}"`);
      const user = await getUserByUsername(minecraftUsername); // Case-insensitive search
      if (!user) {
        console.warn(`authenticateUserAction: Login failed - user "${minecraftUsername}" not found.`);
        return { message: '用户不存在。请检查您的用户名或进行注册。', errors: { minecraftUsername: ['该用户不存在。'] }, success: false };
      }
      // IMPORTANT: For prototype, direct password comparison. In production, use bcrypt.compare.
      if (!user.password || user.password !== password) { 
        console.warn(`authenticateUserAction: Login failed - incorrect password for user "${minecraftUsername}".`);
        return { message: '密码不正确。', errors: { password: ['密码不正确。'] }, success: false };
      }
      console.log(`authenticateUserAction: Login successful for user "${user.username}" (ID: ${user.id}). Setting cookie.`);
      cookies().set('userId', user.id, { httpOnly: true, secure: process.env.NODE_ENV === 'production', maxAge: MAX_AGE, path: '/', sameSite: 'lax' });
      
    } else if (authMode === 'register') {
      console.log(`authenticateUserAction: Attempting registration for user "${minecraftUsername}"`);
      // Redundant password match check as Zod refine should catch it, but good for clarity
      if (validatedFields.data.password !== validatedFields.data.confirmPassword) {
         console.warn(`authenticateUserAction: Registration failed - passwords do not match for "${minecraftUsername}".`);
        return { message: "两次输入的密码不匹配。", errors: { confirmPassword: ["两次输入的密码不匹配。"] }, success: false };
      }

      const isNameMCValid = await checkNameMC(minecraftUsername);
      if (!isNameMCValid) {
        console.warn(`authenticateUserAction: Registration failed - NameMC validation failed for "${minecraftUsername}".`);
        return { message: '此 Minecraft 用户名在 NameMC 上无效或无法验证。', errors: { minecraftUsername: ['此 Minecraft 用户名在 NameMC 上无效或无法验证。'] }, success: false };
      }
      console.log(`authenticateUserAction: NameMC validation successful for "${minecraftUsername}".`);

      const existingUser = await getUserByUsername(minecraftUsername); // Case-insensitive search
      if (existingUser) {
        console.warn(`authenticateUserAction: Registration failed - username "${minecraftUsername}" already exists.`);
        return { message: '此 Minecraft 用户名已被注册。', errors: { minecraftUsername: ['此 Minecraft 用户名已被注册。'] }, success: false };
      }
      console.log(`authenticateUserAction: Username "${minecraftUsername}" is available.`);
      
      let newUser: User | null = null;
      try {
        newUser = await apiCreateUser({ 
          id: minecraftUsername, 
          username: minecraftUsername,
          password: password, 
          op: 0, 
          avatarUrl: `https://placehold.co/100x100.png?text=${minecraftUsername.substring(0,2).toUpperCase()}`
        });
        console.log(`authenticateUserAction: User "${minecraftUsername}" created successfully (ID: ${newUser?.id}).`);
      } catch (error: any) {
        console.error("authenticateUserAction - Registration - apiCreateUser failed:", error);
        return { message: `创建用户时出错: ${error.message || '未知错误'}`, errors: { form: [`创建用户时出错: ${error.message || '未知错误'}`] }, success: false };
      }

      if (!newUser || !newUser.id) {
         console.error("authenticateUserAction: Registration failed - newUser object or ID is null after creation.");
         return { message: '创建用户后未能获取用户信息。', errors: { form: ['创建用户后未能获取用户信息。'] }, success: false };
      }
      console.log(`authenticateUserAction: Registration successful for user "${newUser.username}" (ID: ${newUser.id}). Setting cookie.`);
      cookies().set('userId', newUser.id, { httpOnly: true, secure: process.env.NODE_ENV === 'production', maxAge: MAX_AGE, path: '/', sameSite: 'lax' });
    } else {
      console.error(`authenticateUserAction: Invalid authMode "${authMode}".`);
      return { message: "无效的操作模式。", success: false, errors: {authMode: ["无效的操作模式。"]} };
    }

  } catch (error: any) {
    if (error && typeof error === 'object' && 'digest' in error && typeof error.digest === 'string' && error.digest.startsWith('NEXT_REDIRECT')) {
      console.log("authenticateUserAction: Redirecting...");
      throw error;
    }
    console.error("authenticateUserAction - Top-level Server Error:", error);
    return { message: '认证过程中发生服务器内部错误，请稍后再试。', success: false, errors: { form: ['认证过程中发生服务器内部错误，请稍后再试。']} };
  }
  console.log("authenticateUserAction: Redirecting to /dashboard.");
  redirect('/dashboard');
}


export async function logout() {
  cookies().delete('userId');
  redirect('/login');
}

export async function getSessionUser(): Promise<User | null> {
  const userId = cookies().get('userId')?.value;
  if (!userId) return null;
  try {
    const user = await getUserById(userId); 
    return user || null;
  } catch (error) {
    console.error("获取会话用户失败:", error);
    return null;
  }
}

// --- Event Actions ---
const CreateEventSchema = z.object({
  name: z.string().min(3, "活动名称至少需要3个字符。"),
  date: z.string().refine((date) => !isNaN(Date.parse(date)), "无效的日期格式。"),
  description: z.string().min(10, "描述至少需要10个字符。"),
  participantLimit: z.preprocess(
    (val) => (val === "" || val === null || val === undefined ? undefined : Number(val)),
    z.number().int().positive().optional().nullable()
  ),
  cdKey: z.string().min(4, "活动代码至少需要4个字符。").max(20, "活动代码最多20个字符。"),
  imageUrl: z.string().url("无效的图片链接。").optional().or(z.literal('')),
  userPassword: z.string().min(1, "您的账户密码不能为空。")
});

export async function createEventAction(prevState: any, formData: FormData) {
  try {
    const rawFormData = Object.fromEntries(formData.entries());
    const validatedFields = CreateEventSchema.safeParse(rawFormData);

    if (!validatedFields.success) {
      return {
        errors: validatedFields.error.flatten().fieldErrors,
        message: '创建活动失败。请检查所填字段。',
        success: false,
      };
    }

    const user = await getSessionUser();
    if (!user) {
      return { message: '需要认证才能创建活动。请先登录。', errors: { form: ['请先登录。'] }, success: false };
    }
    if (user.op !== 1) {
      return { message: '您没有创建活动的权限。', errors: { form: ['权限不足。'] }, success: false };
    }

    if (!user.password || user.password !== validatedFields.data.userPassword) {
      return {
        errors: { ...(validatedFields.error?.flatten().fieldErrors || {}), userPassword: ['您的账户密码不正确。'] },
        message: '创建活动失败。账户密码验证失败。',
        success: false,
      };
    }

    let newEventId: string | undefined;
    const { userPassword, ...eventDataForApi } = validatedFields.data;
    const newEvent = await apiCreateEvent( 
        eventDataForApi as Omit<Event, 'id' | 'participants' | 'organizerId'>,
        user.id
    );
    newEventId = newEvent.id;
    
    if (newEventId) {
      redirect(`/events/${newEventId}`);
    } else {
      return { message: '创建活动后未能获取活动ID。', errors: { form: ['创建活动后未能获取活动ID。'] }, success: false };
    }
  } catch (error: any) {
    if (error && typeof error === 'object' && 'digest' in error && error.digest?.startsWith('NEXT_REDIRECT')) {
      throw error;
    }
    console.error("Create Event Action - Server Error:", error);
    return { message: '创建活动时发生意外的服务器错误。', errors: { form: ['创建活动时发生意外的服务器错误。'] }, success: false };
  }
}

const UpdateEventSchema = CreateEventSchema.extend({
  eventId: z.string().min(1, "Event ID is required."),
});

export async function updateEventAction(prevState: any, formData: FormData) {
  try {
    const rawFormData = Object.fromEntries(formData.entries());
    const validatedFields = UpdateEventSchema.safeParse(rawFormData);

    if (!validatedFields.success) {
      return {
        errors: validatedFields.error.flatten().fieldErrors,
        message: '更新活动失败。请检查所填字段。',
        success: false,
      };
    }

    const user = await getSessionUser();
    if (!user) {
      return { message: '需要认证才能更新活动。请先登录。', errors: { form: ['请先登录。'] }, success: false };
    }
    if (user.op !== 1) {
      return { message: '您没有更新活动的权限。', errors: { form: ['权限不足。'] }, success: false };
    }
    if (!user.password || user.password !== validatedFields.data.userPassword) {
      return {
        errors: { ...(validatedFields.error?.flatten().fieldErrors || {}), userPassword: ['您的账户密码不正确。'] },
        message: '更新活动失败。账户密码验证失败。',
        success: false,
      };
    }

    const { userPassword, eventId, ...eventDataToUpdate } = validatedFields.data;
    const updatedEvent = await apiUpdateEventById(eventId, eventDataToUpdate as Partial<Omit<Event, 'id' | 'participants' | 'organizerId'>>);
    
    if (!updatedEvent) {
      return { message: '更新活动失败，活动未找到或没有更改。', errors: { eventId: ['活动未找到或没有进行任何更改。'] }, success: false };
    }
    redirect(`/events/${validatedFields.data.eventId}`);

  } catch (error: any) {
     if (error && typeof error === 'object' && 'digest' in error && error.digest?.startsWith('NEXT_REDIRECT')) {
      throw error;
    }
    console.error("Update Event Action - Server Error:", error);
    return { message: '更新活动时发生意外的服务器错误。', errors: { form: ['更新活动时发生意外的服务器错误。'] }, success: false };
  }
}


export async function deleteEventAction(eventId: string, adminPassword: string): Promise<{ success: boolean, message: string }> {
  const user = await getSessionUser();
  if (!user) {
    return { success: false, message: '需要认证才能删除活动。请先登录。' };
  }
  if (user.op !== 1) {
    return { success: false, message: '您没有删除活动的权限。' };
  }
  if (!user.password || user.password !== adminPassword) {
    return { success: false, message: '您的账户密码不正确，无法删除活动。' };
  }

  try {
    const result = await apiDeleteEventById(eventId); 
    return result;
  } catch (error) {
    console.error("删除活动错误:", error);
    return { success: false, message: '在服务器上删除活动失败。请稍后重试。' };
  }
}


export async function joinEventAction(eventId: string, cdKeyInput: string) {
  const user = await getSessionUser();
  if (!user) {
    return { success: false, message: '您必须登录才能加入活动。' };
  }
  try {
    const result = await apiJoinEvent(user.id, eventId, cdKeyInput); 
    return result;
  } catch (error) {
    console.error("加入活动错误:", error);
    return { success: false, message: '加入活动时发生服务器错误。' };
  }
}

export async function joinEventUsingCdKeyOnlyAction(cdKey: string): Promise<{ success: boolean, message: string, event?: Event }> {
  const user = await getSessionUser();
  if (!user) {
    return { success: false, message: '您必须登录才能加入活动。' };
  }

  if (!cdKey || cdKey.trim() === '') {
    return { success: false, message: '请输入有效的活动代码。' };
  }

  const eventToJoin = await getEventByCdKey(cdKey); 
  if (!eventToJoin) {
    return { success: false, message: '具有此活动代码的活动未找到。' };
  }

  try {
    const result = await apiJoinEvent(user.id, eventToJoin.id, cdKey); 
    return result;
  } catch (error) {
    console.error("使用活动代码加入活动错误:", error);
    return { success: false, message: '使用活动代码加入活动时发生服务器错误。' };
  }
}


