
import EventCard from '@/components/event-card';
import { getEvents } from '@/lib/data';
import type { Event } from '@/lib/types';
import { parseISO } from 'date-fns';
import { CalendarX } from 'lucide-react';

export const dynamic = 'force-dynamic';

export default async function HomePage() {
  const allEvents = await getEvents();

  // Sort events by date in descending order (newest first)
  const sortedEvents = allEvents.sort((a, b) => {
    return parseISO(b.date).getTime() - parseISO(a.date).getTime();
  });

  return (
    <div className="container mx-auto px-4 py-8 space-y-8">
      <h1 className="text-3xl font-bold tracking-tight text-foreground">所有活动</h1>

      {sortedEvents.length === 0 ? (
        <div className="flex flex-col items-center justify-center text-center py-12 min-h-[calc(100vh-22rem)]">
          <CalendarX className="h-16 w-16 text-muted-foreground/70 mb-6" />
          <h2 className="text-xl font-semibold text-muted-foreground mb-3">暂无活动</h2>
          <p className="text-muted-foreground max-w-md">
            当前没有任何《我的世界》活动。您可以稍后再回来查看，或者如果您是管理员，可以尝试创建一个新的活动！
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
          {sortedEvents.map(event => (
            <EventCard key={event.id} event={event} />
          ))}
        </div>
      )}
    </div>
  );
}
