
import AppLayout from './(app)/layout';
import ActualHomePageContent from './(app)/page';

// This page ensures that the root path ("/") is rendered using the main AppLayout
// and the HomePage content defined within the (app) route group.
// It effectively delegates the rendering of the homepage to the (app) structure.
export default async function Page() {
  return (
    <AppLayout>
      <ActualHomePageContent />
    </AppLayout>
  );
}
